<template>
  <div class="backbg">
    <div style="float:left">
      <img src="../img/bird.jpg" width="35px" alt style="float:left;" />
      <span style="float:left;" class="vintage4">Hbird System</span>
    </div>
    <!--  -->
    <div style="float:right;margin-top:5px;">
      <el-button round @click="navToPage"></el-button>
      <el-button type="warning" round @click="newPage"></el-button>
      <el-button type="info" round @click="drawer = true"></el-button>
      <el-button type="info" round @click="getSaveList(1)"></el-button>
      <el-button type="primary" round @click="saveData"></el-button>
      <el-button type="success" round @click="downLoadJson"></el-button>
      <el-button type="danger" round @click="()=>{this.deleteComponent(this.showList)}"></el-button>
    </div>
    <div class="box" @click="clickDown">
      <!--  -->
      <draggable
        :list="startList"
        class="leftBox"
        :options="options"
        @clone="clone"
        @remove="remove"
      >
        <p v-for="(item,index) in startList" :key="index" class="p">{{item.name}}</p>
      </draggable>
      <!--  -->
      <draggable
        :list="showList"
        class="centerBox"
        :options="{group: { name: 'abc' }}"
        @choose="(e)=>{choose(e,this.showList)}"
      >
        <template v-for="(t,i) in showList">
          <!-- form  -->
          <component
            :key="i"
            :is="t.componentName"
            v-bind="t.data"
            v-if="t.componentName=='hb-form'"
            :data-key="t.key"
          >
            <!--   -->
            <hbFormItem
              :comData="t.children"
              :childrenOptions="t.childrenOptions"
              :rules="t.rules"
              @chooseElement="chooseElement"
              @changeItem="changeItem"
            />
          </component>

          <!-- grid -->
          <component
            :key="i"
            :is="t.componentName"
            v-bind="t"
            :comData="t"
            v-else-if="t.componentName=='hb-grid'"
            :data-key="t.key"
            @chooseElement="chooseElement"
            @changeItem="changeItem"
          ></component>

          <!-- form -->
          <component
            :key="i"
            :is="t.componentName"
            :comData="t.data"
            v-bind="t.data"
            v-else
            :data-key="t.key"
          ></component>
        </template>
      </draggable>
      <!--  -->
      <div class="rightBox">
        <div style="margin-top:20px">
          <h4></h4>
        </div>
        <div
          v-if="!currentData.name"
          style="text-align:center;font-size:13px;color: gray;margin-top:22px;"
        ></div>
        <showAtr :currentData="currentData" @setCurrentData="setCurrentData" v-else />
      </div>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible2" width="30%" :before-close="handleClose">
      <span>
         :
        <el-input v-model="pageName" placeholder="" style="width:60%"></el-input>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible2 = false"> </el-button>
        <el-button type="primary" @click="addPage"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-drawer :visible.sync="drawer" direction="ltr" :before-close="handleClose" size="15%">
      <el-tree :data="showList" :props="defaultProps"></el-tree>
    </el-drawer>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="50%"
      :before-close="handleClose"
    >
      <el-table :data="gridData" style="width: 100%" border>
        <el-table-column property="name" label=""></el-table-column>
        <el-table-column property="key" label=""></el-table-column>
        <el-table-column property="value" label="">
          <template slot-scope="scope">
            <span>{{ scope.row.value.length>10?scope.row.value.substring(0,10)+'...':scope.row.value }}</span>
          </template>
        </el-table-column>
        <el-table-column property="createTime" label="">
          <template slot-scope="scope">
            <span>{{ renderTime(scope.row.createTime)}}</span>
          </template>
        </el-table-column>
        <el-table-column property="updateTime" label="">
          <template slot-scope="scope">
            <span>{{ renderTime(scope.row.updateTime)}}</span>
          </template>
        </el-table-column>

        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleClick(scope.row)" type="text"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        layout="prev, pager, next"
        :total="totalCount"
        style="float:right;"
        :page-size="10"
        @current-change="changePage"
      ></el-pagination>
    </el-dialog>
  </div>
</template>

<script>
import draggable from "vuedraggable";
import appMix from "../app.js";
import showAtr from "../basicsComponents/showAtr";
import hbirdOptions from "@hbird/comps/dist/hbirdOptions.json";
export default {
  components: {
    draggable,
    showAtr,
  },
  mixins: [appMix],
  mounted() {
    this.startList = hbirdOptions;
  },
  data() {
    return {
      gridData: [], //
      activeName: "first",
      dialogVisible: false,
      dialogVisible2: false,
      drawer: false,
      pageName: "", //
      options: {
        sort: false,
        dragClass: "pDrag",
        chosenClass: "chosenClass",
        group: { name: "abc", pull: "clone", put: false },
      },
      showList: [], //
      startList: [], //
      currentData: {}, //,
      chooseSaveKey: "", //key
      defaultProps: {
        children: "children",
        label: "name",
      },
      totalCount: null, //
      iDelete: document.createElement("i"), //icon
    };
  },
  methods: {
    chooseElement(e) {
      this.choose(e, this.showList);
    },
    choose(e, arr) {
      arr &&
        arr.forEach((t) => {
          if (e.item.dataset.key == t.key) {
            this.currentData = t;
          } else {
            this.choose(e, t.children);
          }
        });
      this.showDeleteIcon(e.item);
    },
    remove(e) {
      this.startList[e.oldIndex] = JSON.parse(
        JSON.stringify(this.startList[e.oldIndex])
      );
      let num = 0;
      this.showList.forEach((t, d) => {
        console.log(t.children);
        //  form  form 
        t.children &&
          t.children.forEach((w, i) => {
            if (w.children) {
              w.children.forEach((x, y) => {
                if (x.name == "") {
                  w.children.splice(y, 1);
                  this.$message.error("form");
                }
              });
            }
            if (w.name == "" || w.name == "") {
              t.children.splice(i, 1);
              this.$message.error("");
            }
          });
        if (t.name == "") num += 1;
        if (num > 1) {
          this.showList.splice(d, 1);
          this.$message.error(
            ","
          );
        }
      });
    },
    clone(e) {
      this.startList = [];
      this.startList = hbirdOptions;
      let _obj = JSON.parse(JSON.stringify(this.startList[e.oldIndex]));
      //id
      _obj.key = new Date().getTime();
      this.startList[e.oldIndex] = _obj;
    },
  },
};
</script>

<style scoped>
.vintage4 {
  color: gold;
  font-size: 30px;
  letter-spacing: 0;
  text-shadow: 0px 1px 0px #999, 0px 2px 0px #888, 0px 3px 0px #777,
    0px 4px 0px #666, 0px 5px 0px #555, 0px 6px 0px #444, 0px 7px 0px #333,
    0px 8px 7px #001135;
}

.box {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50px;
  left: 0;
  opacity: 0.9;
}
.btnGroup {
  position: absolute;
  right: 0;
}

.p {
  padding: 10px 10px 10px 2px;
  margin: 2px;
  text-align: center;
  background: lightblue;
  border-radius: 20px;
}
.pDrag {
  width: 100%;
  padding: 20px 20px 20px 2px;
  text-align: center;
  background: white;
  border: 3px dashed lightskyblue;
}
.chosenClass {
  width: 100%;
  padding: 20px 20px 20px 2px;
  text-align: center;
  background: lightcyan;
  box-shadow: 0 0 10px rgb(6, 155, 241);
  opacity: 0.5;
}
.box {
  display: flex;
  width: 100%;
}
.leftBox {
  width: 10%;
  /* border-top: 1px solid black;
  border-bottom: 1px solid black;
  border-left: 1px solid black; */
}
.centerBox {
  width: 100%;
  min-height: 100%;
  border: 1px solid black;
  left: "15%";
  /* position: "absolute"; */
}
.rightBox {
  width: 15%;
  background: white;
  right: -2px;
  border: 1px solid black;
  text-align: center;
}
</style>
