<template>
  <!--  -->
  <div style="padding:15px">
    <el-form label-width="80px">
      <h3>{{currentData.name}}</h3>
      <template v-for="(item,i) in currentData.options">
        <!--  -->
        <el-form-item :label="item.name" :key="i" v-if="item.type == 'input'">
          <el-input v-model="item.value" @blur="sendPull"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item :label="item.name" :key="i" v-if="item.type == 'switch'">
          <el-switch v-model="item.value" @change="sendPull"></el-switch>
        </el-form-item>
        <!--  -->
        <el-form-item :label="item.name" :key="i" v-if="item.type == 'select'">
          <el-select v-model="item.value" placeholder="" @change="sendPull">
            <el-option v-for="t in item.arrays" :key="t.value" :value="t.value"></el-option>
          </el-select>
        </el-form-item>
      </template>
    </el-form>
  </div>
</template>
<script>
export default {
  props: {
    currentData: {
      type: Object,
    },
  },
  methods: {
    sendPull() {
      let _obj = {};
      //_obj
      for (let x in this.currentData.options) {
        _obj[x] = this.currentData.options[x].value;
      }
      //_obj
      this.currentData.data = { ...this.currentData.data, ..._obj };
      let sureOptions = {};
      //sureOptions
      for (let x in this.currentData.data) {
        if (
          this.currentData.data[x] !== undefined &&
          this.currentData.data[x] !== ""
        )
          sureOptions[x] = this.currentData.data[x];
      }
      this.currentData.data = sureOptions;
      this.$emit("setCurrentData", this.currentData);
    },
  },
};
</script>
