<template>
  <hb-input :placeholder="placeholder" :rule="rule" v-bind="comData" :comData="comData" />
</template>
<script>
export default {
  name: "hb-email",
  props: {
    comData: Object,
    placeholder: {
      //,
      type: String,
      default: ""
    }
  },
  data() {
    return {
      rule: /^([a-zA-Z]|[0-9])(\w|)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/ //
    };
  }
};
</script>
